import React, { lazy } from "react";
import Image from "../../container/Image/Image";
import Footer from "../Footer/Footer";
import "./Gallery.css";
// import img from "./images/gallery-images";

const Gallery = () => {
	function importAll(r) {
		return r.keys().map(r);
	}
	const images = Array.from(
		importAll(
			require.context(
				"../../../public/images/gallery-images",
				false,
				/\.(png|jpe?g|svg)$/
			)
		)
	);
	const pictures = {
		image1: ["./images/gallery-images/image1.jpeg", "Descp"],
		image2: ["./images/gallery-images/image2.jpeg", "Descp"],
		image3: ["./images/gallery-images/image3.jpeg", "Descp"],

		image5: ["./images/gallery-images/image14.jpeg", "Descp"],

		image6: ["./images/gallery-images/image15.jpeg", "Descp"],
		image7: ["./images/gallery-images/image16.jpeg", "Descp"],
		image8: ["./images/gallery-images/image17.jpeg", "Descp"],
		image9: ["./images/gallery-images/image18.jpeg", "Descp"],
		image4: ["./images/gallery-images/image19.jpeg", "Descp"],
		image20: ["./images/gallery-images/image20.jpeg", "Descp"],
		image10: ["./images/gallery-images/image4.jpeg", "Descp"],
		image11: ["./images/gallery-images/image5.jpeg", "Descp"],

		image13: ["./images/gallery-images/image6.jpeg", "Descp"],
		image14: ["./images/gallery-images/image7.jpeg", "Descp"],
		image15: ["./images/gallery-images/image8.jpeg", "Descp"],
		image16: ["./images/gallery-images/image9.jpeg", "Descp"],
		image17: ["./images/gallery-images/image10.jpeg", "Descp"],
		image18: ["./images/gallery-images/image11.jpeg", "Descp"],
		image19: ["./images/gallery-images/image12.jpeg", "Descp"],
		image20: ["./images/gallery-images/image13.jpeg", "Descp"],
	};
	const postsItems = [];
	Object.values(pictures).forEach((each, index) => {
		// eslint-disable-next-line
		postsItems.push(
			<div key={index}>
				<div className="image-container">
					<div className="image">
						<img src={each[0]} alt="" />
					</div>
					<div className="title"> {each[1]}</div>
				</div>
			</div>
		);
	});

	console.log(postsItems[1]);

	return (
		<div className="gallery-container">
			<div className="gallery-main-container">
				<div className="gallery-title">
					<h1>Pictures</h1>
				</div>
				<div className="gallery-grid">{postsItems}</div>
			</div>

			<Footer />
		</div>
	);
};

export default Gallery;
